@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700;800&display=swap);
:root {
  --nav-color: hsl(25, 90%, 20%);
  --secondary-color: #03045e;
}
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 64.5%;
}
body {
  font-size: 1.6rem;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media screen and (max-width: 720px) {
  html {
    font-size: 50%;
  }
}

.nav {
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--nav-color);
  color: white;
}
.nav__heading {
  font-family: "Pacifico", sans-serif;
  font-size: 4rem;
  padding-left: 20px;
}
.nav__list {
  display: flex;
  justify-content: space-evenly;
  width: 50%;
  list-style-type: none;
  font-size: 1.8rem;
  font-weight: 600;
}
.nav__list li {
  cursor: pointer;
  padding: 10px 20px;
  border-radius: 10px;
  transition: all 0.5s;
}

.nav__list li:hover {
  background-color: white;
  color: var(--nav-color);
}

.form {
  width: 50%;
  margin: 0 auto;
  background-color: var(--secondary-color);
  padding: 20px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  margin-bottom: 20px;
}

.form__subheading {
  color: white;
  font-size: 2rem;
}
.form__personalInformation,
.form__experience,
.form__education,
.form__passions {
  padding: 10px 0;
}
.input__row {
  display: flex;
}
.form__buttons {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 20px 0;
}
.form__buttons button {
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  border: none;
  outline: none;
  background-color: var(--nav-color);
  color: white;
  font-size: 1.6rem;
  transition: all 0.5s;
}
.form__buttons button:hover {
  background-color: var(--secondary-color);
}
.error {
  background-color: var(--nav-color);
  color: white;
  padding: 10px 28px;
  margin: 10px 0;
  border: 1px solid rgba(255, 255, 255, 0.6);
  border-top: 0;
  border-left: 0;
  display: none;
  transition: all 0.5s;
}
.error.active {
  display: block;
}
.note {
  font-size: 1.4rem;
  color: rgb(189, 142, 142);
}

@media screen and (max-width: 720px) {
  .form {
    width: 80%;
  }
}

.input {
  color: white;
  width: 100%;
  padding: 5px 0;
}
.input label {
  display: block;
  padding: 4px;
}
.input input {
  outline: none;
  padding: 5px 6px;
  width: calc(100% - 9px);
  margin-left: 3px;
  font-size: 1.6rem;
}
.input input:invalid {
  background-color: red;
}
.input.half {
  width: 50%;
}

.preview {
  width: 60%;
  margin: 20px auto;
}
.preview__page {
  margin: 15px 0;
  background-color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  padding: 40px;
  font-family: "Rubik", sans-serif;
}
.preview__wrapper {
  display: flex;
}
.preview__personalInformation {
  padding-bottom: 20px;
}
.preview__personalInformation a {
  text-decoration: none;
  color: black;
}
.heading {
  font-weight: bold;
  text-transform: uppercase;
}
.name {
  font-size: 3.8rem;
}
.short_description {
  font-size: 1.9rem;
  color: var(--secondary-color);
  font-weight: bold;
}
.other_information {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  font-size: 1.5rem;
}
.phoneNo,
.email,
.github_website,
.location {
  display: flex;
  align-items: center;
  padding: 5px 0;
}
.preview__left {
  flex: 0.6 1;
  padding-right: 30px;
}
.sub_heading {
  font-size: 2.8rem;
  padding: 5px 0;
}
.hr {
  height: 4px;
  width: 100%;
  background-color: black;
  border-radius: 5px;
}
.vr {
  width: 5px;
  height: 68px;
  margin: 0 10px;
  border-radius: 5px;
  background-color: black;
}
.exp_container,
.edu_container {
  padding: 10px 0;
}
.edu_container {
  display: flex;
  align-items: center;
}
.inline-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  opacity: 0.8;
}
.exp_heading,
.edu_heading {
  font-size: 2.3rem;
}
.exp_companyName,
.edu_university {
  color: var(--secondary-color);
  font-weight: 800;
  text-transform: capitalize;
  letter-spacing: 1.5px;
}
.edu_university {
  padding: 10px 0;
}
.exp_dates,
.edu_dates {
  display: flex;
  align-items: center;
  margin: 0 5px;
}
.inline-wrapper i {
  margin-right: 10px;
}
.date {
  padding: 0 3px;
}
.preview__right {
  flex: 0.35 1;
}
.edu_score {
  text-align: center;
  font-size: 1.65rem;
  padding: 5px 0;
}
.obtained {
  color: var(--secondary-color);
  font-weight: bold;
}
.obtained,
.total {
  padding: 0 6px;
}
.passion {
  padding: 10px 0;
}
.passion_name {
  padding: 4px 0;
}
.br_dashed {
  border-bottom: 1px dashed black;
}
.languages {
  padding: 15px 0;
}
.language {
  display: inline-block;
  margin: 4px 8px;
  padding: 4px 6px;
  border-bottom: 1px solid black;
}
/* ICONS */
.icon {
  box-sizing: content-box;
  color: var(--secondary-color);
  margin-right: 10px;
}
.icon-phone {
  box-sizing: border-box;
  position: relative;
  display: block;
  width: 22px;
  height: 22px;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transform: scale(var(--ggs, 1));
          transform: scale(var(--ggs, 1));
}

.icon-phone::after,
.icon-phone::before {
  content: "";
  display: block;
  box-sizing: border-box;
  position: absolute;
}

.icon-phone::after {
  width: 18px;
  height: 18px;
  border-top-left-radius: 1px;
  border-bottom-right-radius: 1px;
  border-bottom-left-radius: 12px;
  border-left: 4px solid;
  border-bottom: 4px solid;
  left: 2px;
  bottom: 2px;
  background: linear-gradient(to left, currentColor 10px, transparent 0)
      no-repeat right 11px/6px 4px,
    linear-gradient(to left, currentColor 10px, transparent 0) no-repeat -1px 0/4px
      6px;
}
.icon-email {
  position: relative;
  display: block;
}
.icon-email::before {
  content: "@";
  font-family: sans-serif;
  font-size: 1.7rem;
}

.icon-browser {
  box-sizing: border-box;
  position: relative;
  display: block;
  width: 14px;
  height: 14px;
  border: 2px solid;
  border-top: 0;
  border-bottom-left-radius: 100px;
  border-bottom-right-radius: 100px;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transform: scale(var(--ggs, 1));
          transform: scale(var(--ggs, 1));
  --ggs: 0.8;
  margin-top: 11px;
}

.icon-browser::after,
.icon-browser::before {
  content: "";
  display: block;
  box-sizing: border-box;
  position: absolute;
  border-radius: 3px;
  border: 2px solid;
}

.icon-browser::after {
  border-bottom: 0;
  border-top-left-radius: 100px;
  border-top-right-radius: 100px;
  right: -2px;
  width: 10px;
  height: 14px;
  bottom: 8px;
}

.icon-browser::before {
  width: 6px;
  height: 12px;
  border-top: 0;
  border-bottom-left-radius: 100px;
  border-bottom-right-radius: 100px;
  left: 2px;
  bottom: 4px;
}

.icon-location {
  width: 28px;
  object-fit: contain;
}
.icon_calendar {
  box-sizing: border-box;
  position: relative;
  display: block;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transform: scale(var(--ggs, 1));
          transform: scale(var(--ggs, 1));
  width: 18px;
  height: 18px;
  border: 2px solid;
  border-radius: 3px;
}

.icon_calendar::after,
.icon_calendar::before {
  content: "";
  display: block;
  box-sizing: border-box;
  position: absolute;
  border-radius: 3px;
  height: 2px;
  left: 2px;
}

.icon_calendar::before {
  background: currentColor;
  width: 2px;
  box-shadow: 4px 0 0, 8px 0 0, 0 4px 0, 4px 4px 0, 8px 4px 0;
  top: 6px;
}

.icon_calendar::after {
  width: 10px;
  top: -4px;
  box-shadow: 0 6px 0 0;
}

@media print {
  body {
    font-size: 16pt;
  }
  .nav {
    display: none;
  }
  .preview {
    width: 100%;
    margin: 0;
  }
  .preview__page {
    position: absolute;
    width: 100%;
    height: 100%;
    margin: 0;
    border-radius: 0;
    box-shadow: none;
  }
}

@media screen and (max-width: 950px) {
  .preview {
    width: 86%;
  }
}
@media screen and (max-width: 650px) {
  .preview {
    width: 94%;
  }
}

